@import url(https://fonts.googleapis.com/css2?family=Georama:wght@300;400;500;600&display=swap);
.App {
	width: 550px;
	margin: 0 auto;
}


.post {
    margin: 20px 0 20px 0;
    padding: 25px 15px 25px 15px;
    background-color: rgb(29, 44, 58);
    border: 1px solid rgb(109, 109, 109);
    border-radius: 20px;
    display: flex;
    flex-flow: column nowrap;
    font-family: 'Georama', sans-serif;
    color: rgb(255, 255, 255);
}

.post img, .post div {
    margin: 0 10px 0 10px;
}

.post-body {
    display: flex;
}

.author-photo {
    width: 10%;
    height: 10%;
    border-radius: 50%;
}

.article {
    display: flex;
    flex-flow: column nowrap;
    align-items: flex-start;
    line-height: 28px;
}

.author-name {
    font-size: 16px;
    font-weight: 600;
}

.author-nickname, .date {
    font-weight: 600;
    font-size: 14px;
    color: rgba(137, 137, 137, 0.814);
}
 
.post-text {
    font-size: 15px;
}

.article .post-photo {
    width: 100%;
    margin: 12px 0 12px 0;
    border-radius: 35px;
}

.actions {
    display: flex;
    flex-flow: row nowrap;
    justify-content: space-between;
    align-items: center;
    padding: 0 30px 0 100px;
    color: rgba(158, 157, 157, 0.814);
}

.actions span {
    display: flex;
    align-items: center;
}

.actions img {
    width: 20px;
    height: 20px;
}

@media (max-width: 650px) {
    .post {
        flex-flow: column nowrap;
        align-items: center;
    }

    .author-photo {
        width: 15%;
        height: 15%;
        border-radius: 50%;
    }

}

@media (max-width: 650px) {
    .post {
        padding: 10px 10px 25px 10px;
        background-color: rgb(29, 44, 58);
    }

    .author-photo {
        width: 22%;
        height: 22%;
    }
    
    .article {
        display: flex;
        flex-flow: column nowrap;
        align-items: flex-start;
        line-height: 20px;
    }

    .author-name {
        font-size: 14px;
    }
    
    .author-nickname, .date {
        font-size: 12px;
    }
     
    .post-text {
        font-size: 13px;
    }
    
    .article .post-photo {
        border-radius: 20px;
    }
    
    .actions img {
        width: 16px;
        height: 16px;
    }
}
.addPostWrapper {
	display: flex;
	flex-flow: column nowrap;
	background-color: rgb(29, 44, 58);
	border: 1px solid rgb(109, 109, 109);
	padding: 20px 15px 20px 15px;
	color: rgb(255, 255, 255);
	border-bottom-left-radius: 20px;
	border-bottom-right-radius: 20px;
}

select,
textarea,
input {
	margin: 5px 0 5px 0;
	padding: 5px;
    font-family: "Georama", sans-serif;
}

textarea {
    resize: none;
}

button {
	margin: 15px 0 0 0;
	padding: 5px;
	width: 200px;
	align-self: center;
	font-weight: 600;
	text-transform: uppercase;
}

.btn-grad {background-image: linear-gradient(to right, #16222A 0%, #3A6073  51%, #16222A  100%)}
.btn-grad {
   margin: 10px;
   padding: 8px 45px;
   text-align: center;
   text-transform: uppercase;
   transition: 0.5s;
   background-size: 200% auto;
   color: white;            
   box-shadow: 0 0 10px #eee;
   border-radius: 10px;
   display: block;
 }

 .btn-grad:hover {
   background-position: right center; /* change the direction of the change here */
   color: #fff;
   text-decoration: none;
 }



